"use client"

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Home, Clock, Palette, User } from 'lucide-react';
import { cn } from '@/lib/utils';

const navItems = [
  { href: '/mood', icon: Home, label: 'Home' },
  { href: '/history', icon: Clock, label: 'History' },
  { href: '/themes', icon: Palette, label: 'Themes', disabled: true },
  { href: '/profile', icon: User, label: 'Profile', disabled: true },
];

export default function BottomNav() {
  const pathname = usePathname();

  return (
    <nav className="w-full bg-background/80 backdrop-blur-sm border-t border-border -mx-6 mt-auto px-6">
      <div className="flex justify-around items-center h-20">
        {navItems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              href={item.disabled ? '#' : item.href}
              key={item.label}
              className={cn(
                'flex flex-col items-center justify-center gap-1 w-16 text-muted-foreground transition-colors duration-300',
                isActive && 'text-primary',
                item.disabled ? 'opacity-50 cursor-not-allowed' : 'hover:text-primary'
              )}
              aria-disabled={item.disabled}
              onClick={(e) => item.disabled && e.preventDefault()}
            >
              <item.icon className="w-6 h-6" />
              <span className="text-xs">{item.label}</span>
            </Link>
          );
        })}
      </div>
    </nav>
  );
}
